* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
  background-color: #282c34; 
  color: #f4f4f4;
}

.title{
  font-size: 2.8rem;
  color: #f5f5f5;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  letter-spacing: 0.5rem;
}

.category{
  font-size: 1.5rem;
  color: #69748d;
}

input{
  color: #f3f3f3;
  background-color: #20232a; 
  outline: none;
  width: 100%;
  font-size: 1.2rem;
  border: 1px solid;
  padding: 10px;
  box-shadow: 2px 3px 6px #f4f4f4;
  margin: 10px 0;
}

input::placeholder{
  color: #686868;
}

.card-grid{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 6px;
  object-fit: cover;
  margin: 0 10px 10px 0;
  padding: 10px;
  overflow: hidden;
  width: 200px;
  height: 200px;
  
  background-color: #69748d;
}

.card p {
  /* padding: 5px; */
  text-align: center;
}

.card img {
  max-height: 150px;
  max-width: 200px;

}

.loading{
  font-size: 3rem;
  color: red;
}

.btn{
  border-radius: 10px;
  background-color: #69748d;
  border: 1px solid #000;
  padding: 7px 10px;
  color: #282c34;
  margin: 15px 0;
}

.after-title{
  font-size: 2rem;
  font-family: 'Roboto';
  letter-spacing: 1px;
  font-style: italic;
}



footer span a{
  color: #aa75c9;
  text-decoration: none;
}